/* 초기화 */
* {
    margin: 0;
    padding: 0;
    font-family: SUITE-Regular;
}

ul,li,ol {
    list-style: none;
}
a {
    text-decoration: none;
    color: #111;
}
input,
textarea {
    outline: none;
}
#root > div > h3 {
    font-family: ONE-Mobile-POP;
    margin-top:30px;
}
strong {
    font-family: ONE-Mobile-POP;
    font-weight: normal;
    color: #A21A24;
}
#wrapper {
    width: 100%;
}
.brandIntro {
    position: relative;
    padding: 20px;
}
.brandIntro::before {
    content: "";
    background-image: url(../../images/brand/intro.png);
    background-size: cover;
    opacity: 0.5;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom:0px;
}
.brandIntro p {
    line-height: 30px;
    color:black;
    font-weight: bold;
    text-shadow: -1px 0px yellow, 0px 1px yellow, 1px 0px yellow, 0px -1px yellow;
    position: relative;
}

.container {padding-bottom: 20px;}

.link_menu {
    position: fixed;
    right:1.5rem;
    z-index: 25;
    bottom:calc(100% - 700px);
}
